.media{
   display: flex;
   flex-direction: column;
   width: 250px;
   padding: 5px;
   margin: 5px 0;
   background-color: rgb(92, 175, 214);
   border-radius: 10px;
   position: relative;
   margin-bottom: 2rem;
   font-family: fantasy; 
   transition: all 0.3s ease;
   cursor: pointer;
}
.media:hover{
    background-color: rgb(32,126,169);

    color: red;
}
.media:hover .poster{
    transition: all 1s linear;
    transform:scale(1.01);
}
@media (max-width : 550px) {
    .media{
        width: 46%;
    }
}
.poster{
    border-radius: 10px;
}

.title{
    width: 100%;
    text-align: center;
    font-size: 17px;
    padding: 8px 0;
    color: black;
    
}
.subTitle{
    display: flex;
    justify-content: space-between;
    padding-bottom: 4px;
    color: black;
    padding: 0 2px;
}