.header{
    width: 100%;
    cursor: pointer;
    position: fixed;
    display: flex;
    justify-content: center;
    text-transform: uppercase;
    background-color: rgb(36,37,38);
    font-size: 4vw;
    padding-bottom: 15px;
    box-shadow: 0 1px 5px black;
    color: aliceblue;
    z-index: 100;
}
@media (max-width : 1200) {
    .header{
        padding-top: 15px;
        font-size: 6.4vw;
    }
}