*{
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
  scroll-behavior: smooth;
}
.App{
  min-height: 100vh;
  background-color: rgb(223, 228, 234);
  color: white;
  padding-top: 130px;
  padding-bottom: 70;

}
@media (max-width : 700px){
  .App{
    padding-top: 70px;
  }
}
.pageTitle{
  text-transform: uppercase;
  display: flex;
  justify-content: center;
  font-family: fantasy;
  font-size: 3vw;
  padding: 4px;
  margin-bottom: 2rem;
border-radius: 50px;
color:rgb(0,0,0);
font-weight: 100;
}
@media (max-width : 1000px) {
  .pageTitle{
    font-size: 6.4vw;
  }
}